<h2>Form Attributes</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>model</td>
<td>表单数据对象</td>
<td>object</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>rules</td>
<td>表单验证规则</td>
<td>object</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>rules.prop1[0].trigger</td>
<td>表单验证触发时机</td>
<td>change/blur</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>inline</td>
<td>行内表单模式</td>
<td>boolean</td>
<td>---</td>
<td>false</td>
</tr>
<tr>
<td>label-position</td>
<td>表单域标签的位置</td>
<td>string</td>
<td>right/left/top</td>
<td>right</td>
</tr>
<tr>
<td>label-width</td>
<td>表单域标签的宽度，作为 Form 直接子元素的 form-item 会继承该值</td>
<td>string</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>label-suffix</td>
<td>表单域标签的后缀</td>
<td>string</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>show-message</td>
<td>是否显示校验错误信息</td>
<td>boolean</td>
<td>---</td>
<td>true</td>
</tr>
<tr>
<td>inline-message</td>
<td>是否以行内形式展示校验信息</td>
<td>boolean</td>
<td>---</td>
<td>false</td>
</tr>
<tr>
<td>status-icon</td>
<td>是否在输入框中显示校验结果反馈图标</td>
<td>boolean</td>
<td>---</td>
<td>false</td>
</tr>
<tr>
<td>validate-on-rule-change</td>
<td>是否在 rules 属性改变后立即触发一次验证</td>
<td>boolean</td>
<td>---</td>
<td>true</td>
</tr>
<tr>
<td>size</td>
<td>用于控制该表单内组件的尺寸</td>
<td>string</td>
<td>medium / small / mini</td>
<td>---</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用该表单内的所有组件。若设置为 true，<br>则表单内组件上的 disabled 属性不再生效</td>
<td>boolean</td>
<td>---</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Form Methods</h2>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>validate</td>
<td>对整个表单进行校验的方法，参数为一个回调函数。该回调函数会在校验结束后被调用，<br>并传入两个参数：是否校验成功和未通过校验的字段。若不传入回调函数，则会返回一个 promise</td>
<td>Function(callback: Function(boolean, object))</td>
</tr>
<tr>
<td>validateField</td>
<td>对部分表单字段进行校验的方法</td>
<td>Function(prop: string, callback: Function(errorMessage: string))</td>
</tr>
<tr>
<td>resetFields</td>
<td>对整个表单进行重置，将所有字段值重置为初始值并移除校验结果</td>
<td>---</td>
</tr>
<tr>
<td>clearValidate</td>
<td>移除整个表单的校验结果</td>
<td>---</td>
</tr>
</tbody>
</table>
<h2>Form Events</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>validate</td>
<td>任一表单项被校验后触发</td>
<td>被校验的表单项 prop 值，校验是否通过</td>
</tr>
</tbody>
</table>
<h2>Form-Item Attributes</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>prop</td>
<td>表单域 model 字段，在使用 validate、resetFields 方法的情况下，该属性是必填的</td>
<td>string</td>
<td>传入 Form 组件的 model 中的字段</td>
<td>---</td>
</tr>
<tr>
<td>label</td>
<td>标签文本</td>
<td>string</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>label-width</td>
<td>表单域标签的的宽度，例如 '50px'</td>
<td>string</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>required</td>
<td>是否必填，如不设置，则会根据校验规则自动生成</td>
<td>boolean</td>
<td>---</td>
<td>false</td>
</tr>
<tr>
<td>rules</td>
<td>表单验证规则</td>
<td>object</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>error</td>
<td>表单域验证错误信息, 设置该值会使表单验证状态变为error，并显示该错误信息</td>
<td>string</td>
<td>---</td>
<td>---</td>
</tr>
<tr>
<td>show-message</td>
<td>是否显示校验错误信息</td>
<td>boolean</td>
<td>---</td>
<td>true</td>
</tr>
<tr>
<td>inline-message</td>
<td>是否以行内形式展示校验信息</td>
<td>boolean</td>
<td>---</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>用于控制该表单内组件的尺寸</td>
<td>string</td>
<td>medium / small / mini</td>
<td>---</td>
</tr>
</tbody>
</table>
<h2>Form-Item Slot</h2>
<table class="table"><thead>
<tr>
<th>slot name</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>---</td>
<td>Form Item 的内容</td>
</tr>
<tr>
<td>label</td>
<td>标签文本的内容</td>
</tr>
</tbody>
</table>
<h2>Form-Item Methods</h2>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>resetField</td>
<td>对整个表单进行重置，将所有字段值重置为初始值并移除校验结果</td>
<td>---</td>
</tr>
</tbody>
</table>
</article></section></template>
